import React, { useState } from "react";
import "./Style.scss";
import TextField from "@mui/material/TextField";
import { Container, makeStyles } from "@material-ui/core";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import { createNewProduct } from "../../../../API/goodsAPI";

export default function index(): JSX.Element {
  const clearForm = () => {
    const nImput = document.getElementById("name-input");
    if (nImput) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      nImput.value = "";
    }
    const pImput = document.getElementById("price-input");
    if (pImput) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      pImput.value = "0";
    }
    const fImput = document.getElementById("file-input");
    if (fImput) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      fImput.value = "";
    }
  };
  const useStyles = makeStyles({
    field: { marginTop: 1, marginBottom: 20, display: "block" },
    input: {
      marginTop: 10,
      marginBottom: 20,
      display: "block",
      marginRight: "auto",
      marginLeft: "auto",
    },
  });
  const classes = useStyles();

  const [name, setName] = useState("");
  const [price, setPrice] = useState("0");
  const [file, setFile] = useState<File>();
  const [type, setType] = useState("img");

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    const inputFile = e.nativeEvent.target.files[0];
    const fileType = inputFile.type.split("/")[0];
    if (fileType == "video") {
      setType("vid");
    }
    setFile(inputFile);
  };

  const createFormData = () => {
    if (name && price && file) {
      const formData = new FormData();
      formData.append("name", name);
      formData.append("price", price);
      formData.append("type", type);
      formData.append("img", file);
      return formData;
    } else {
      console.log("formData error");
      return null;
    }
  };

  const sendData = async () => {
    const data = createFormData();
    if (data) {
      const res = await createNewProduct(data);
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      if (res.status) {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        alert(res.message);
        setName("");
        setPrice("0");
        setFile(undefined);
        clearForm();
        return;
      }
      setName("");
      setPrice("0");
      setFile(undefined);
      clearForm();
      alert("Create successfully");
    }
  };

  return (
    <Container className="new-product-container">
      <h1>Create new product</h1>
      <Box
        component="form"
        noValidate
        autoComplete="off"
        sx={{
          "& .MuiTextField-root": {
            mt: 1,
            width: "30ch",
            display: "block",
            mr: "auto",
            ml: "auto",
          },
        }}
      >
        <TextField
          className={classes.field}
          onChange={(e) => setName(e.target.value)}
          id="name-input"
          label="Add name"
          variant="filled"
          required
          size="small"
          InputProps={{ disableUnderline: true }}
        />
        <TextField
          className={classes.field}
          onChange={(e) => setPrice(e.target.value)}
          id="price-input"
          label="Add price"
          type="number"
          required
          InputLabelProps={{
            shrink: true,
          }}
          variant="filled"
          defaultValue="0"
          size="small"
          InputProps={{ disableUnderline: true }}
        />

        <input
          accept="image/* , video/*"
          className={classes.input}
          type="file"
          id="file-input"
          onChange={(e) => handleFileChange(e)}
        />
        <Stack direction="row" spacing={2}>
          <Button
            variant="contained"
            color="success"
            onClick={() => {
              sendData();
            }}
          >
            Add
          </Button>
          <Button
            variant="outlined"
            color="error"
            type="reset"
            onClick={() => {
              setName("");
              setPrice("0");
              setFile(undefined);
            }}
          >
            Cancel
          </Button>
        </Stack>
      </Box>
    </Container>
  );
}
